.gutenberghub-query-taxonomy {
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	border: none;

	-webkit-box-orient: vertical;

	-webkit-box-direction: normal;

	-ms-flex-direction: column;

	flex-direction: column;
	&.flex-direction-column {
		max-height: var(--ghub-qbt-height);
		overflow-y: auto;
	}
	.gutenberghub-taxonomy-group {
		display: -webkit-box;
		display: -ms-flexbox;
		display: flex;
		-webkit-box-orient: vertical;
		-webkit-box-direction: normal;
		-ms-flex-direction: column;
		flex-direction: column;
		width: 100%;
		border: 1px solid #e0e0e0;
		margin-bottom: 20px;
		.gutenberghub-taxonomy-item {
			border-bottom: 1px solid #f0f0f0;
			padding: 12px 16px;
			&:hover {
				background-color: #f0f0f0;
			}
		}
	}
}
